

























import { Component, Prop, PropSync, Vue, Watch } from 'vue-property-decorator';
import { Delete, Get } from '@/plugins/axios';
import { IOrder } from '@/interfaces/order';

@Component
export default class OrdersList extends Vue {
  private orders: IOrder[] = [] as any;
  @Prop() private driver: any;
  @PropSync('dialogUpdate') private dialog: any;
  @PropSync('selectOrder') private cs: any;
  @Watch('dialog') private async onDialog(val: boolean) {
    if (!val) await this.updateList();
  }
  @Watch('driver.id') private async onCustomerId(id: any) {
    if (id === 0 || id === undefined || id === null) return;
    await this.updateList();
  }
  private async updateList() {
    if (!this.driver) {
      this.orders = await Get('orders', this);
      return;
    }
    const url = `orders/driver/${this.driver.id}`;
    this.orders = await Get(url, this);
  }
  private async del(id: number) {
    await Delete(`orders/${id}`, this);
    await this.updateList();
  }
  private async mounted() {
    this.orders = await Get('orders', this);
  }
}
