






















import { Component, Vue } from 'vue-property-decorator';
import OrdersList from '@/components/orders/OrdersList.vue';
import OrdersButtonExcel from '@/components/orders/OrdersButtonExcel.vue';
import OrdersInput from '@/components/orders/OrdersInput.vue';
import ProductList from '@/components/orders/OrdersProductListDialog.vue';
import { IDriver } from '@/interfaces/driver';
import { Get } from '@/plugins/axios';
import { ICustomer } from '@/interfaces/customer';

@Component({
  components: {
    ProductList,
    OrdersInput,
    OrdersButtonExcel,
    OrdersList,
  },
})
export default class Orders extends Vue {
  private drivers: IDriver[] = [];
  private driver: IDriver = null as any;
  private customer: ICustomer = null as any;
  private order = null;
  private dialog: boolean = false;
  private async mounted() {
    this.drivers = await Get('driver', this);
  }
}
