

































































































import { Component, Prop, PropSync, Vue, Watch } from 'vue-property-decorator';
import { IProduct } from '@/interfaces/product';
import { find } from '@/plugins/find';
import { Delete, Get, Post } from '@/plugins/axios';

@Component
export default class OrdersProductListDialog extends Vue {
  @Prop() private order: any;
  @Prop() private driver: any;
  private typeProduct = null as any;
  private typeProducts = [];
  private product: IProduct = null as any;
  private products: IProduct[] = [];
  private count: number = 0;
  private type: string = '';
  private show = false;
  private selectOrder = this.order;
  @PropSync('value', { type: Boolean }) private dialog!: boolean;
  @Watch('typeProduct.id') private onTypeProductId(id: number) {
    const item: any = find(this.typeProducts, id);
    if (item === undefined) {
      this.products = [] as any;
      return;
    }
    this.products = item.products;
  }
  @Watch('order') private onOrder(o: any) {
    this.selectOrder = o;
  }
  private async shower(val: boolean) {
    setTimeout(() => (this.show = val), 500);
  }
  private async close() {
    this.$emit('input', false);
  }
  private async add() {
    const data = {
      product: this.product.id,
      order: this.selectOrder.id,
      type: this.type,
      count: this.count,
    };

    if (data.type === null || data.type === undefined) {
      data.type = '';
    }
    await Post('product-list', data, this);
    setTimeout(() => {
      this.updateList();
    }, 300);
  }
  private async del(id: number) {
    await Delete(`product-list/${id}`, this);
    setTimeout(() => {
      this.updateList();
    }, 300);
  }
  private async updateList() {
    this.selectOrder = await Get(`orders/${this.selectOrder.id}`, this);
    this.typeProducts = this.typeProducts.sort((a: any, b: any) => a.id - b.id).reverse();
  }
  private async mounted() {
    this.typeProducts = await Get('type-product', this);
    await this.shower(true);
  }
}
