






















import { Component, Prop, Vue } from 'vue-property-decorator';
import ProductList from '@/components/orders/OrdersProductListDialog.vue';
import { Dx, Get } from '@/plugins/axios';
import { IDriver } from '@/interfaces/driver';
import { ICustomer } from '@/interfaces/customer';

@Component({
  components: { ProductList },
})
export default class OrdersButtonExcel extends Vue {
  @Prop() private driver: IDriver = null as any;
  @Prop() private customer: ICustomer = null as any;
  private disabled = 1;
  private async excelTotal() {
    const url = 'excel';
    await Dx(url, `Итог_${new Date().toLocaleDateString()}.xls`, this);
  }
  private async excelStore() {
    const url = 'excel/store';
    await Dx(url, `Накладные_${new Date().toLocaleDateString()}.xls`, this);
  }
  private async closeAll() {
    await Get('orders/delete-all', this);
    document.location.reload();
  }
}
