











































































import Vue from 'vue';
import Component from 'vue-class-component';
import { Delete, Get, Post, Put } from '@/plugins/axios';
import { Watch } from 'vue-property-decorator';
import { error } from '@/plugins/toast';
import { find } from '@/plugins/find';

@Component
export default class Customer extends Vue {
  private drivers = [];
  private action = 'Добавить';
  private driverId = 0;
  private driver: any = null;
  private customer: any = { name: '', position: 0, driver: 0 };
  private customers = [];
  private clickDriver(item: any) {
    if (item === undefined) {
      this.driver = null;
      this.driverId = 0;
      return;
    }
    this.driver = item;
    this.driverId = item.id;
  }
  private clickCustomer(item: any) {
    this.customer = item;
    this.action = 'Сохранить';
  }
  private async add() {
    this.customer.driver = this.driver.id;
    if (this.action === 'Добавить') {
      await Post('customer', this.customer, this);
      await this.updateList();
      this.customer.position++;
      return;
    }
    await this.put();
  }
  private async del(id: number) {
    this.action = 'Добавить';
    await Delete(`customer/${id}`, this);
    await this.updateList();
  }
  private async put() {
    this.action = 'Добавить';
    const customer = this.customer;
    const url = `customer/${customer.id}`;
    await Put(url, customer, this);
    await this.updateList();
  }
  private async updateList() {
    this.drivers = await Get('driver', this);
    this.drawList(this.driverId);
  }
  private mounted() {
    this.updateList();
  }
  private drawList(id: number) {
    const item: any = find(this.drivers, id);
    if (item === undefined) {
      return;
    }
    this.customers = item.customers.sort((a: any, b: any) => a.position - b.position).reverse();
  }
  private keydown(e: KeyboardEvent) {
    if (e.key === 'Enter') {
      this.add();
    }
  }
  @Watch('driver.id') private onDriverId(id: number) {
    if (id === undefined || id === 0) {
      this.customers = [];
      return;
    }
    this.drawList(id);
  }
}
