


































































import Vue from 'vue';
import Component from 'vue-class-component';
import { Delete, Get, Post, Put } from '@/plugins/axios';
import { Watch } from 'vue-property-decorator';
import { IGroup } from '@/interfaces/group';
import { IProduct } from '@/interfaces/product';
import { error } from '@/plugins/toast';
import { find } from '@/plugins/find';

@Component
export default class Product extends Vue {
  private groups: IGroup[] = [];
  private group: IGroup = null as any;
  private products: IProduct[] = [];
  private product: IProduct = {} as IProduct;
  private action = 'Добавить';
  private keydown(e: KeyboardEvent) {
    if (e.key === 'Enter') {
      this.add();
    }
  }
  private async add() {
    this.product.parent = this.group.id;
    if (this.action === 'Сохранить') {
      await this.put();
      return;
    }
    await Post(`product`, this.product, this);
    await this.updateList();
  }
  private async del(id: number) {
    this.action = 'Добавить';
    await Delete(`product/${id}`, this);
    await this.updateList();
  }
  private async put() {
    this.action = 'Добавить';
    await Put(`product/${this.product.id}`, this.product, this);
    await this.updateList();
  }
  private async updateList() {
    this.groups = await Get('type-product', this);
    if (this.group) this.drawList(this.group.id);
  }
  private mounted() {
    this.updateList();
  }
  private drawList(id: number) {
    const item: any = find(this.groups, id);
    if (item === undefined) {
      return;
    }
    this.products = item.products.sort((a: any, b: any) => a.position - b.position).reverse();
  }
  @Watch('group.id') private onDriverId(id: number) {
    this.drawList(id);
  }
}
