






































import { Component, Vue } from 'vue-property-decorator';
import { Delete, Get, Post, Put } from '@/plugins/axios';
import { error } from '@/plugins/toast';

@Component
export default class ProductGroup extends Vue {
  private action = 'Добавить';
  private data: any = {};
  private items = [];
  private mounted() {
    this.updateList();
  }
  private keydown(e: KeyboardEvent) {
    if (e.key === 'Enter') {
      this.add();
    }
  }
  private async del(id: number) {
    await Delete(`type-product/${id}`, this);
    await this.updateList();
  }
  private async updateList() {
    this.items = await Get('type-product', this);
  }
  private async add() {
    if (this.action === 'Сохранить') {
      await this.put();
      return;
    }
    await Post('type-product', this.data, this);
    await this.updateList();
  }
  private async put() {
    this.action = 'Добавить';
    await Put(`type-product/${this.data.id}`, this.data, this);
    await this.updateList();
  }
  private clickItemList(item: any) {
    this.action = 'Сохранить';
    this.data = item;
  }
}
