





















































import { Component, Prop, PropSync, Vue, Watch } from 'vue-property-decorator';
import { IDriver } from '@/interfaces/driver';
import { ICustomer } from '@/interfaces/customer';
import { find } from '@/plugins/find';
import { Get, Post } from '@/plugins/axios';

@Component
export default class OrdersInput extends Vue {
  private primary = null;
  private customersList: ICustomer[] = [] as any;
  private customer: ICustomer = null as any;
  private driver: IDriver = null as any;

  @Prop() private driversList!: IDriver[];
  @PropSync('selectCustomer') private sc: any;
  @PropSync('selectDriver') private sd: any;
  @Watch('primary') private async onPrimary(color: string) {
    if (color === 'primary') {
      this.customersList = await Get('customer', this);
    }
  }

  private async onChangeDriver(e: any) {
    if (!e) {
      this.customersList = await Get('customer', this);
      return;
    }
    this.$emit('update:selectDriver', e);
    if (this.primary === 'primary') return;
    this.customersList = find(this.driversList, e.id).customers;
  }
  private onChangeCustomer(e: any) {
    this.$emit('update:selectCustomer', e);
  }
  private async createOrder() {
    const data = {
      customer: this.customer.id,
      driver: this.driver.id,
      status: true,
    };
    await Post('orders', data, this);
    this.$emit('update:selectDriver', { id: 0 });
    setTimeout(() => this.$emit('update:selectDriver', this.driver), 500);
  }
  private async mounted() {
    this.customersList = await Get('customer', this);
  }
}
