import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import Orders from '../views/Orders.vue';
import Product from '@/views/Product.vue';
import Routes from '@/views/Driver.vue';
import Clients from '@/views/Customer.vue';
import ProductGroup from '@/views/ProductGroup.vue';

Vue.use(VueRouter);

const routes: RouteConfig[] = [
  {
    path: '/',
    name: 'Orders',
    component: Orders,
  },
  {
    path: '/product',
    name: 'Product',
    component: Product,
  },
  {
    path: '/group_product',
    name: 'ProductGroup',
    component: ProductGroup,
  },
  {
    path: '/driver',
    name: 'Driver',
    component: Routes,
  },
  {
    path: '/customer',
    name: 'Customer',
    component: Clients,
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
