



































import { Component, Vue } from 'vue-property-decorator';
import { Delete, Get, Post, Put } from '@/plugins/axios';
import { IDriver } from '@/interfaces/driver';

@Component
export default class Driver extends Vue {
  private action = 'Добавить';
  private id = 0;
  private items: IDriver[] = [];
  private text = '';
  private mounted() {
    this.updateData();
  }
  private async updateData() {
    this.items = await Get('driver', this);
  }
  private keydown(e: KeyboardEvent) {
    if (e.key === 'Enter') {
      this.add();
    }
  }
  private clickDriver(item: IDriver) {
    this.text = item.name;
    this.id = item.id;
    this.action = 'Сохранить';
  }
  private async del(id: number) {
    await Delete(`driver/${id}`, this);
    await this.updateData();
  }
  private async add() {
    if (this.action === 'Сохранить') {
      await this.put();
      return;
    }
    const data = { name: this.text };
    await Post('driver', data, this);
    await this.updateData();
  }
  private async put() {
    const data = { name: this.text };
    await Put(`driver/${this.id}`, data, this);
    await this.updateData();
  }
}
